import React, { PropTypes } from 'react';
var Select = require('rc-select');
var Option = Select.Option;


import $ from 'jquery';
class HighSchool {
    render() {
        return (
            <div id="high" className="search-box tab-content-box">
                <dl>
                    <dd>
                        <Select value="" style={{height:45}} optionFilterProp="desc"  renderDropdownToBody={true} onChange={this.handleChange}>
                            <Option value="" desc=""></Option>
                            <Option value="" desc=" "></Option>
                            <Option value="" desc=""></Option>
                            <Option value="" desc=""></Option>
                            <Option value="" desc=""></Option>
                        </Select>
                    </dd>
                    <dd>
                        <input type="text" className="school-name" placeholder=""  name="name" />
                    </dd>
                    <dd>
                        <Select value="" style={{height:45}} optionFilterProp="desc"  renderDropdownToBody={true} onChange={this.handleChange}>
                            <Option value="" desc=""></Option>
                        </Select>
                    </dd>
                </dl>
            </div>
        );
    }

}

export default HighSchool;
