import React, { PropTypes } from 'react';
var Select = require('rc-select');
var Option = Select.Option;
import styles from "./Select.less";
import withViewport from '../../../decorators/withViewport';
import withStyles from '../../../decorators/withStyles';

@withStyles(styles)
class XiangDuDeZhuanYe {

    static contextTypes = {
        onSetTitle: PropTypes.func.isRequired
    };

    render() {
        return (
            <div className="box full">
                <div className="wish-speciality">
                    <h1></h1>
                    <dl>
                        <dt></dt>
                        <dd>
                            <Select value="" style={{width:200,height:45}} optionFilterProp="desc"  renderDropdownToBody={true} onChange={this.handleChange}>
                                <Option value="" desc="" selected="selected"></Option>
                                <Option value="" desc=" "></Option>
                                <Option value="" desc=""></Option>
                            </Select>
                        </dd>
                        <dd className="clear"></dd>
                    </dl>
                    <dl>
                        <dt></dt>
                        <dd>
                            <Select value="" style={{width:200,height:45}} optionFilterProp="desc"  renderDropdownToBody={true} onChange={this.handleChange}>
                                <Option value="" desc="" selected="selected"></Option>
                                <Option value="" desc=" "></Option>
                            </Select>
                        </dd>
                        <dd className="clear"></dd>
                    </dl>
                    <dl>
                        <dt></dt>
                        <dd>
                            <input type="text" className="keyword" /></dd>
                        <dd className="clear"></dd>
                    </dl>
                    <dl>
                        <dt>&nbsp;</dt>
                        <dd>
                            <button className="btn-search button-normal"></button></dd>
                    </dl>
                </div>
            </div>
        );
    }
}

export default XiangDuDeZhuanYe;



